tileset-menu{flex-direction:column;width:136px;}tileset-menu form{display:flex;align-items:flex-start;justify-content:center;margin:10px 0;width:100%}.tilesets__add{width:34px;height:34px;padding:.5em 6px 0;margin-left:5px;text-align:left;background-color:#9baec8;color:#000;cursor:pointer;}.tilesets__add:hover{background-color:#d9e1e8}.tilesets__add:active{background-color:#2b90d9;color:#fff}.tilesets__list{user-select:none}.tilesets__item{width:64px;height:64px;margin-right:8px;margin-bottom:8px;text-align:center;line-height:62px;}.tilesets__item:nth-child(2n){margin-right:0}.tilesets__item i.icon-plus{font-size:24px}.tilesets__preview{border-top:1px solid #ccc;text-align:center;}.tilesets__preview button{margin:10px 0 0}